.OutputContainer{
    width: 40%;
    height: 100%;
    background: white;
    display: flex;
    align-items: baseline;
    box-shadow: 4px 10px 15px 3px lightgrey;
}

.OutputContainer p{
    font-size: 1.1rem;
    margin: 0;
}

.OutputContainer h1{
    margin: 0.4rem 0.4rem 0.4rem 2rem;

}

@media only screen and (max-width: 600px) {
    .OutputContainer h1{
        margin: 0.5rem;    
    }

    .OutputContainer{
        justify-content: center;
    }
  }